.home {
    &__exp-edu {
        padding-top: 111px;
        padding-bottom: 248px;
    }
    &__line {
        position: absolute;
        left: 0;
        width: 100%;
        z-index: 2;
        &--top {
            top: -290px;
        }
        &--bot {
            bottom: -409px;
        }
    }
    &__exp-edu-inner {
        position: relative;
        z-index: 3;
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
    }
    .experience {
        max-width: 560px;
    }
    .education {
        width: 270px;
    }
    &__pic-man {
        position: sticky;
        top: 0;
        right: 172px;
        width: 500px;
    }
}
