.education {
    text-align: center;

    &__main-title {
        font-weight: 700;
        font-size: 24px;
        margin-bottom: 30px;
    }

    &__list {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &__item {
        width: 190px;
        padding: 0 20px;
        margin-bottom: 33px;
        &:last-child {
            margin-bottom: 0;
        }
    }
    &__icons {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 40px;
        margin-bottom: 12px;
    }
    &__icon {
        width: 15px;
        height: 15px;
        border: 2px solid;
        border-radius: 4px;
        position: relative;
        transform: rotate(45deg);
        margin-right: 5px;
        &:last-child {
            margin-right: 0;
        }

        @include active {
            transform: rotate(45deg) scale(2);
            z-index: 3;
        }

        &--violet {
            border-color: #ccc9fd;
            background-image: linear-gradient(45deg, #afaafe 50%, #928cf8 50% 100%);
        }
        &--yellow {
            border-color: #fff3b5;
            background-image: linear-gradient(45deg, #ffe975 50%, #ffcd3d 50% 100%);
        }
        &--pink {
            border-color: #ff99be;
            background-image: linear-gradient(45deg, #fe74a6 50%, #ee5087 50% 100%);
        }
        &--bluish {
            border-color: #c0f0ff;
            background-image: linear-gradient(45deg, #79cafb 50%, #4bbef2 50% 100%);
        }
        &--orange {
            border-color: #ffd5b4;
            background-image: linear-gradient(45deg, #ffbc89 50%, #ffa25a 50% 100%);
        }
        &--green {
            border-color: #7cf5d2;
            background-image: linear-gradient(45deg, #66dfbc 50%, #3dc39b 50% 100%);
        }
    }
    &__title {
        font-weight: 700;
        line-height: 1.3;
        margin-bottom: 5px;
    }
}
