.main-banner {
    padding-top: 92px;
    padding-bottom: 90px;
    position: relative;
    z-index: 2;

    &__line {
        position: absolute;
        top: -81px;
        right: 187px;
        width: 100px;
    }

    &__inner {
        position: relative;
    }

    &__top {
        margin-bottom: 40px;
    }

    &__pic {
        width: 150px;
        height: 150px;
        img {
            height: 100%;
            object-fit: contain;
        }
    }

    &__content {
        display: flex;
        justify-content: space-between;
    }

    &__texts {
        max-width: 560px;
    }

    &__title {
        font-weight: 700;
        font-size: 36px;
        margin-bottom: 30px;
        line-height: 1.4;
    }

    &__discription {
        font-size: 14px;
        line-height: 1.4;
    }

    &__contact {
        margin-right: 150px;
    }

    &__contact-list {
        margin-bottom: 26px;
        li {
            font-size: 14px;
            margin-bottom: 10px;
        }
    }

    &__social {
        display: flex;
        li {
            margin-right: 20px;
            &:last-child {
                margin-right: 0;
            }
        }
        span {
            display: inline-block;
            width: 25px;
            height: 25px;
            svg {
                object-fit: contain;
            }
        }
    }
    &__preview-man {
        position: absolute;
        bottom: 0;
        right: 0;
        width: 400px;
    }
}
